@import url(https://fonts.googleapis.com/css2?family=Alice&family=Iceberg&family=Raleway:ital,wght@0,400;1,500&family=Roboto:ital,wght@0,300;0,400;0,500;0,700;0,900;1,300;1,400;1,500;1,700;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

/* body {
  background-color: #EFF4F7;
} */

.contact-page {
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;
}

.required {
  font-family: 'Iceberg', cursive;
  font-size: 1em;
  padding-top: 1em;
  display: block;
  text-align: center;
}

.contact-form-intro{
  font-family: 'Alice', serif;
  font-size: 1.5em;
  line-height: 1.2em;
  color: #523524;
  margin: 1em 24em 2em 24em;
}

.form-container {
  width: 100vw;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding-top: 5em;
  padding-bottom: 2em;
  background-image: url('https://raw.githubusercontent.com/buzzfair/cultcodeSmr2020Cohort/master/images/logo-trans_500x500.png');
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  background-color: #c4d8d6;
  align-items: center;
  justify-content: center;
}

