#contact-form {
  margin-left: auto;
  margin-right: auto;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  border: solid 1pt #523524;
  border-radius: 5px;
  text-align: left;
  margin-bottom: 5em;
  display: block;
  /* background-image: url('https://raw.githubusercontent.com/buzzfair/cultcodeSmr2020Cohort/master/images/logo-trans_500x500.png');
   */
  /* background-color:#c4d8d6; */
}

fieldset {
  border: none;
}

label {
  font-family: 'Iceberg', cursive;
  padding: .5em;
  display: block;
  color: #523524;
}

#inputFirstName, #inputLastName {
  height: 2em;
  width: 15em;
  border-radius: 5px;
  border: .5px #54405B solid;
}

#inputEmail {
  height: 2em;
  width: 30em;
  border-radius: 5px;
  border: .5px #54405B solid;
}

#inputMessage {
  height: 15em;
  width: 30em;
  border-radius: 5px;
  border: .5px #54405B solid;
}

#fs-button {
  text-align: center;
}

.con-response {
  font-family: 'Iceberg', cursive;
  font-size: 1.1em;
  color: #c7742b;
}

button {
  padding: .5em 1.5em;
  font-family: 'Iceberg', cursive;
  font-size: 1.25em;
  color: #eff4f7;
  background-color: #523524;
  border-radius: 10px;
}

.content-section {
  margin-bottom: 50px;
  flex: 1 0 auto;
}

.Footer {
  margin-top: -70px;
  height: 70px;
  background-color: #EFF4F7;
  display: flex;
  flex-shrink: 0;
  width: 100%;
  justify-content: flex-start;
  align-items: center;
}

.footer-text {
  font-family: 'Alice', serif;
  font-size: .9em;
  padding: 1em 2em;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.header {
    display: flex;
    justify-content: center;
    align-items: center;
    max-height: 3em;
}

.header__poem {
    display: flex;
    max-height: 1em;
    justify-content: start;
    align-items: flex-start;
}

.poem-text {
    padding-top: .5em;
    padding-left: 3em;
    font-family: 'Alice', serif;
    font-size: 1.25em;
    color: #523524;
}

.logo{
    height: 13rem;
    max-height: 13rem;
    min-height: 13rem;
}

.header__logo {
    height: 12rem;

}

.header__logo:active {
    height: 13rem;
}

.header__social-media {
    display: flex;
    flex-direction: row-reverse;
    max-height: 1em;
}

.header__smicon{
    align-self: end;
    justify-self: end;
    padding-top: .5em;
    padding-left: 1.5em;
    padding-right: 1.5em;
    padding-bottom: 0;
}

.header__smicon:hover {
    transform: translateY(3px);
    height: 40px;
    width: 40px;
}

.navbar {
    margin-top: .5rem;
    height: 3.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #523524;
}


.navbar__link {
    font-family: 'Alice', serif;
    color: #EFF4F7;
    text-decoration: none;
    margin: 0 200px;
    font-size: 1.25rem;
}

.navbar__link:hover {
    transform: translateY(3px);
    font-size: 1.35em;
}

/* project details popup styling */

.content__label-container {
    background-color: #523524;
    padding: 2em;
    width: 80vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}

.content__key {
    font-family: 'Iceberg', cursive;
    color: #EFF4F7;
    font-size: 1.25em;
    letter-spacing: 2px;
    margin: 0em 1.5em;
}


.content__value {
    font-family: 'Alice', serif;
    color: #EFF4F7;
    font-size: 1.15em;
}

.content__main-image {
    width: 450px;
    height: auto;
    border: #C7742B 25px solid;
    border-radius: 10px;
}

.content__prototype,
.content__myProcess {
    width: 600px;
    height: auto;
    border-radius: 10px;
}

.content__userJourney {
    width: 750px;
    height: auto;
    border: 1px solid #EFF4F7 ;
    border-radius: 10px;
}

.content__wireframes,
.content__userPersonas {
    width: 600px;
    height: auto;
    display: block;
    border-radius: 10px;
}

.content__iteration {
    width: 500px;
    height: auto;
    display: block;
    border-radius: 10px;
}

figcaption {
    font-family: 'Alice', serif;
    color: #C4D8D6;
    font-size: .85em;
    font-style: italic;
    margin-bottom: 1em;

}
.content {
    margin-top: 4em;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.content__image {
    height: 15rem;
    width: auto;
    margin: auto;
}

.content__paragraph {
    color: #424c55;
    font-size: 1.2em;
    margin: .5em 25em;
}

.content__home {
    text-align: center;
    color: #424c55;
    font-size: 1.2em;
    margin: auto;
}

.content__additional {
    text-align: center;
    color: rgb(185, 63, 63);
    font-size: 1.2em;
}

.image__additional {
    height: 15rem;
    width: auto;
    margin: auto;
}



p {
    margin: 2em 0;
    text-align: center;
    color: #523524;
    font-size: 1.2rem;
    align-self: center;
}

.title {
    display: flex;
    height: 8em;
    justify-content: center;
    align-items: center;

}

.title__descriptors {
    font-family: 'Iceberg', cursive;
    font-size: 2em;
    color: #523524;

}

.title__descriptors:hover {
    transform: scale(1.5);
    color: #54405B;

}

.material-icons {
    color: #C7742B;
    padding: 0 5px;
}

.material-icons:hover {
    transform: scale(2);
    color: #54405B;
}

.icon-carousel {
    height: 300px;
    background-color: #C4D8D6;
    padding: 50px 85px;
    text-decoration: none;
    border: none;
}

.carousel-slides {
    margin-top: 2em;
    height: 200px;
    text-decoration: none;
    border: none;
} 

.carousel-images {
    height: 150px;
    width: auto;
    text-decoration: none;
    border: none;
}

.carousel-images:hover {
    height: 160px;
    text-decoration: none;
    border: none;
}

.about-me {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 30em;
    background-image: url('https://raw.githubusercontent.com/buzzfair/cultcodeSmr2020Cohort/master/images/logo-trans_500x500.png');
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
}

