.header {
    display: flex;
    justify-content: center;
    align-items: center;
    max-height: 3em;
}

.header__poem {
    display: flex;
    max-height: 1em;
    justify-content: start;
    align-items: flex-start;
}

.poem-text {
    padding-top: .5em;
    padding-left: 3em;
    font-family: 'Alice', serif;
    font-size: 1.25em;
    color: #523524;
}

.logo{
    height: 13rem;
    max-height: 13rem;
    min-height: 13rem;
}

.header__logo {
    height: 12rem;

}

.header__logo:active {
    height: 13rem;
}

.header__social-media {
    display: flex;
    flex-direction: row-reverse;
    max-height: 1em;
}

.header__smicon{
    align-self: end;
    justify-self: end;
    padding-top: .5em;
    padding-left: 1.5em;
    padding-right: 1.5em;
    padding-bottom: 0;
}

.header__smicon:hover {
    transform: translateY(3px);
    height: 40px;
    width: 40px;
}
