
/* project details popup styling */

.content__label-container {
    background-color: #523524;
    padding: 2em;
    width: 80vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}

.content__key {
    font-family: 'Iceberg', cursive;
    color: #EFF4F7;
    font-size: 1.25em;
    letter-spacing: 2px;
    margin: 0em 1.5em;
}


.content__value {
    font-family: 'Alice', serif;
    color: #EFF4F7;
    font-size: 1.15em;
}

.content__main-image {
    width: 450px;
    height: auto;
    border: #C7742B 25px solid;
    border-radius: 10px;
}

.content__prototype,
.content__myProcess {
    width: 600px;
    height: auto;
    border-radius: 10px;
}

.content__userJourney {
    width: 750px;
    height: auto;
    border: 1px solid #EFF4F7 ;
    border-radius: 10px;
}

.content__wireframes,
.content__userPersonas {
    width: 600px;
    height: auto;
    display: block;
    border-radius: 10px;
}

.content__iteration {
    width: 500px;
    height: auto;
    display: block;
    border-radius: 10px;
}

figcaption {
    font-family: 'Alice', serif;
    color: #C4D8D6;
    font-size: .85em;
    font-style: italic;
    margin-bottom: 1em;

}