.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

/* body {
  background-color: #EFF4F7;
} */

.contact-page {
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;
}

.required {
  font-family: 'Iceberg', cursive;
  font-size: 1em;
  padding-top: 1em;
  display: block;
  text-align: center;
}

.contact-form-intro{
  font-family: 'Alice', serif;
  font-size: 1.5em;
  line-height: 1.2em;
  color: #523524;
  margin: 1em 24em 2em 24em;
}

.form-container {
  width: 100vw;
  height: fit-content;
  padding-top: 5em;
  padding-bottom: 2em;
  background-image: url('https://raw.githubusercontent.com/buzzfair/cultcodeSmr2020Cohort/master/images/logo-trans_500x500.png');
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  background-color: #c4d8d6;
  align-items: center;
  justify-content: center;
}

