#contact-form {
  margin-left: auto;
  margin-right: auto;
  width: max-content;
  border: solid 1pt #523524;
  border-radius: 5px;
  text-align: left;
  margin-bottom: 5em;
  display: block;
  /* background-image: url('https://raw.githubusercontent.com/buzzfair/cultcodeSmr2020Cohort/master/images/logo-trans_500x500.png');
   */
  /* background-color:#c4d8d6; */
}

fieldset {
  border: none;
}

label {
  font-family: 'Iceberg', cursive;
  padding: .5em;
  display: block;
  color: #523524;
}

#inputFirstName, #inputLastName {
  height: 2em;
  width: 15em;
  border-radius: 5px;
  border: .5px #54405B solid;
}

#inputEmail {
  height: 2em;
  width: 30em;
  border-radius: 5px;
  border: .5px #54405B solid;
}

#inputMessage {
  height: 15em;
  width: 30em;
  border-radius: 5px;
  border: .5px #54405B solid;
}

#fs-button {
  text-align: center;
}

.con-response {
  font-family: 'Iceberg', cursive;
  font-size: 1.1em;
  color: #c7742b;
}

button {
  padding: .5em 1.5em;
  font-family: 'Iceberg', cursive;
  font-size: 1.25em;
  color: #eff4f7;
  background-color: #523524;
  border-radius: 10px;
}

.content-section {
  margin-bottom: 50px;
  flex: 1 0 auto;
}

.Footer {
  margin-top: -70px;
  height: 70px;
  background-color: #EFF4F7;
  display: flex;
  flex-shrink: 0;
  width: 100%;
  justify-content: flex-start;
  align-items: center;
}

.footer-text {
  font-family: 'Alice', serif;
  font-size: .9em;
  padding: 1em 2em;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@import url('https://fonts.googleapis.com/css2?family=Alice&family=Iceberg&family=Raleway:ital,wght@0,400;1,500&family=Roboto:ital,wght@0,300;0,400;0,500;0,700;0,900;1,300;1,400;1,500;1,700;1,900&display=swap');