.navbar {
    margin-top: .5rem;
    height: 3.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #523524;
}


.navbar__link {
    font-family: 'Alice', serif;
    color: #EFF4F7;
    text-decoration: none;
    margin: 0 200px;
    font-size: 1.25rem;
}

.navbar__link:hover {
    transform: translateY(3px);
    font-size: 1.35em;
}