.projects {
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: center;
    align-items: center;
}

.projects__wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 80vw;
    margin-bottom: 3em;
}


.projects__nav {
    margin: 2em 0;
    display: flex;
}


.projects__image {
    height: 375px;
    width: auto;
    margin: 1em 2em;
    border: .5px #C7742B solid;
    transition: .5s ease;
    opacity: 1;
    backface-visibility: hidden;

}

.projects__image-middle {
    transition: .5s ease;
    opacity: 0;
    position: absolute;
    top: 75%;
    left: 25%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    text-align: center;
}

.projects__nav:hover .projects__image {
    opacity: 0.3;
}

.projects__nav:hover .projects__image-middle {
    opacity: 1;
}

.projects__image-middle-text {
    color: #523524;
    font-family: 'Alice', serif;
    padding: 16px 32px;
}

.projects__copy {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    padding: 2em 0;
}

.projects__name {
    color: #523524;
    font-family: 'Iceberg', cursive;
    font-size: 2em;
}

.projects__desc {
    color: #523524;
    font-family: 'Alice', serif;
    font-size: 1.3em;
    margin: 0 5em;
    text-align: justify;
}


.content-projects {
    position: relative;
    display: none;
    flex-direction: column;

}

.content__key,
.content__value {
    margin: 1em 2.5em;
}

.popup-content {
    background-color: #523524 !important;
    position: absolute !important;
    border-radius: 20px !important;
    width: 82% !important;
    padding: 2em !important;
    height: 85% !important;
    left: 7% !important;
    right: 50px !important;
    overflow: scroll !important;
  }

  .close,
  .close:hover,
  .close:active {
    cursor: pointer;
    font-family: inherit;
    color:#EFF4F7;
    font-size: 2em;
    font-weight: 800;
  }

button {
    width: 125px;
    height: 60px;
    margin-left: auto;
    margin-right: auto;
    font-size: 1.1em;
    margin-top: 1.5em;
    cursor: pointer;
}