p {
    margin: 2em 0;
    text-align: center;
    color: #523524;
    font-size: 1.2rem;
    align-self: center;
}

.title {
    display: flex;
    height: 8em;
    justify-content: center;
    align-items: center;

}

.title__descriptors {
    font-family: 'Iceberg', cursive;
    font-size: 2em;
    color: #523524;

}

.title__descriptors:hover {
    transform: scale(1.5);
    color: #54405B;

}

.material-icons {
    color: #C7742B;
    padding: 0 5px;
}

.material-icons:hover {
    transform: scale(2);
    color: #54405B;
}

.icon-carousel {
    height: 300px;
    background-color: #C4D8D6;
    padding: 50px 85px;
    text-decoration: none;
    border: none;
}

.carousel-slides {
    margin-top: 2em;
    height: 200px;
    text-decoration: none;
    border: none;
} 

.carousel-images {
    height: 150px;
    width: auto;
    text-decoration: none;
    border: none;
}

.carousel-images:hover {
    height: 160px;
    text-decoration: none;
    border: none;
}

.about-me {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 30em;
    background-image: url('https://raw.githubusercontent.com/buzzfair/cultcodeSmr2020Cohort/master/images/logo-trans_500x500.png');
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
}

